import { mapState } from "vuex";

export default {
    data() {
        return {
            express_no: '',
            express: '',
            customer_name: '',
            code: '',
            words: '',
            list: [],
            dialog: false,
            id: '',
            value: 0,
            is_send: 0,
            mch_id: 0,
            express_id: 0,
            order_refund_id: 0,
            order_detail_ids: [],
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info
        })
    },
    methods: {
        bindChange(e) {
            let index = +e.detail.value[0]
            this.express = this.list[index].name
        },
        toPick(type) {
            let that = this;
            if(type == 1) {
                that.customer_name = '';
                for(let index in that.list) {
                    if (that.list[index].name == that.express) {
                        that.value = +index;
                    }
                }
            }
            that.express = that.list[that.value].name
            that.dialog = !that.dialog;
        },
        getExpressNo() {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            that.$request({
                url: that.$api.mch.order_print,
                data: {
                    express: that.express,
                    code: that.code,
                    mch_id: that.mch_id,
                    order_id: that.id,
                    order_detail_ids: JSON.stringify(that.order_detail_ids)
                },
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.express_no = response.data.Order.LogisticCode;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        getExpress() {
            let that = this;
            that.$showLoading({
                text: '加载中...'
            });
            that.$request({
                url: that.$api.mch.express_list
            }).then(response=>{
                if(response.code == 0) {
                    that.list = response.data.list;
                    that.getList();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.mch.order_detail,
                data: {
                    id: that.id,
                    mch_id: that.mch_id,
                }
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    let detail = response.data.detail.detailExpress[0];
                    if(detail != undefined) {
                        that.express = detail.express ? detail.express : response.data.detail.last_express ? response.data.detail.last_express : that.list[0].name;
                        that.express_no = detail.express_no ? detail.express_no : '';
                        that.customer_name = detail.customer_name ? detail.customer_name : '';
                        that.express_id = detail.id ? detail.id : '';
                        that.code = detail.code > 0 ? detail.code : '';
                        that.words = detail.words ? detail.words : '';
                    }else {
                        that.express = response.data.detail.last_express ? response.data.detail.last_express : that.list[0].name;
                    }
                    that.order_detail_ids = response.data.detail.detail.map(item => {
                        return item['id']
                    });
                    for(let index in that.list) {
                        if (that.list[index].name == that.express) {
                            that.value = +index;
                        }
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        saveAddress: function (e) {
            let that = this;
            if (!that.express_no) {
                uni.showToast({
                    title: '快递单号不得为空',
                    icon: 'none',
                    duration: 1000,
                });
                return;
            }
            uni.showLoading({
                title: '提交中...'
            });
            if(that.id > 0) {
                let para ={
                    is_express: 1,
                    order_id: that.id,
                    express_no: that.express_no,
                    mch_id: that.mch_id,
                    express: that.express,
                    code: that.code,
                    customer_name: that.customer_name,
                    words: that.words,
                }
                if(that.express_id) {
                    para.express_id = that.express_id
                }
                that.$request({
                    url: that.$api.mch.order_send,
                    method: "POST",
                    data: para
                }).then(response => {
                    uni.hideLoading();
                    if (response.code == 0) {
                        let msg = response.msg;
                        if(that.is_send == 2) {
                            msg = '修改成功'
                        }
                        uni.showToast({
                            title: msg,
                            duration: 1000,
                            type: 'success',
                            mask: false
                        });
                        setTimeout(function(){
                            uni.navigateBack();
                        },500)
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });   
            }else {
                let para ={
                    is_express: 1,
                    type: 2,
                    is_agree: 1,
                    mch_id: that.mch_id,
                    order_refund_id: that.order_refund_id,
                    express_no: that.express_no,
                    express: that.express,
                    merchant_remark: that.words,
                }
                if(para.express_id) {
                    para.express_id = that.express_id
                }
                that.$request({
                    url: that.$api.mch.refund_handle,
                    method: "POST",
                    data: para
                }).then(response => {
                    uni.hideLoading();
                    if (response.code == 0) {
                        let msg = response.msg;
                        uni.showToast({
                            title: msg,
                            duration: 1000,
                            type: 'success',
                            mask: false
                        });
                        setTimeout(function(){
                            uni.navigateBack();
                        },500)
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            }

        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        if (options.id > 0) {
            that.id = options.id
        }
        that.mch_id = options.mch_id;
        if (options.order_refund_id > 0) {
            that.order_refund_id = options.order_refund_id
        }
        that.getExpress();
        if (options.is_send > 0) {
            that.is_send = options.is_send
        }
    }
}
